<template lang="pug">
    .test__answer-item
        .test__number {{answerChar}}
        .test__answer-text
            textarea(@change='updateItem()' v-model='answerItem' :disabled='!testEditable').test__answer
        vue-mathjax(:formula='answerItem' v-if='texTranslateVisible').answer__formula
</template>
<script>
import {VueMathjax} from 'vue-mathjax'
export default {
    components: {
        'vue-mathjax': VueMathjax
    },
    props: {
        answer: String,
        answerChar: String,
        texTranslateVisible: Boolean,
        testEditable: Boolean
    },
    data() {
        return {
            answerItem: this.answer,
            currentTestEditable: this.testEditable  
        }
    },
    methods: {
        updateItem(){
            this.$emit('updateAnswerItem', {
                answerChar: this.answerChar,
                answer: this.answerItem
            })
        }
    }
}
</script>
<style lang="scss">
.mjx-chtml{ font-size: 100% !important; }
.mjx-math { font-size: 100% !important; }
